@import "main-bx";

.intro {
    position: relative;
  

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 0;

        @include below(590px) {
            position: static;
        }
    }

    &__content {
        position: relative;
        z-index: 10;
        padding: 376px 0 40px;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include below(1450px) {
            padding: 276px 0 40px;
        }

        @include below(1240px) {
            padding: 240px 0 40px;
        }

        @include below(992px) {
            padding: 160px 0 40px;
        }

        @include below(590px) {
            padding: 0;
            position: absolute;
            top: 200px;
            left: 50%;
            transform: translate(-50%, 0);
        }

        @include below(479px) {
            top: 95%;
        }
    }

    &__title {
        color: $orange_color;
        text-align: center;
        font-size: 140px;
        font-style: normal;
        font-weight: 700;
        line-height: 85%;
        text-transform: uppercase;

        @include below(1430px) {
            font-size: 120px;
        }

        @include below(1340px) {
            font-size: 100px;
        }

        @include below(1240px) {
            font-size: 80px;
        }

        @include below(992px) {
            font-size: 70px;
        }

        @include below(768px) {
            font-size: 60px;
        }

        @include below(590px) {
            font-size: 50px;
            white-space: nowrap;
        }

        @include below(479px) {
            font-size: 40px;
        }
     
    }

    &__btn {
        color: $orange_color;
        margin-top: 110px;

        &:hover:not(:active) {
            color: #D70403;
        }
        svg {
            width: 62px;
            height: 131px;

            @include below(1430px) {
                width: 50px;
                height: 120px;
            }

            @include below(1240px) {
                width: 50px;
                height: 90px;
            }

            @include below(992px) {
                width: 40px;
                height: 75px;
            }
        }

        @include below(590px) {
           display: none;
        }
    }
}