@import "main-bx";

.form {

    &__title {
        font-size: 24px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 10px;

        @include below(768px) {
            font-size: 20px;
        }

        @include below(540px) {
            font-size: 18px;
            margin-bottom: 12px;
        }

        &_second {
            margin: 40px 0 24px;
        }
    }

    &__text {
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 120%;
        color: #000;
        margin-bottom: 24px;

        @include below(768px) {
            margin-bottom: 12px;
        }
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        margin: 12px -20px 0;

        @include below(768px) {
            margin: 0;
        }
    }

    &__label {
        display: block;
        position: relative;
        width: calc(50% - 40px);
        margin: 0 20px;

        @include below(768px) {
            width: 100%;
            margin: 12px 0 0;
        }
    }

    &__input {
        width: 100%;
        background-color: $white_color;
        padding: 15px 12px;
        height: 46px;
        border-radius: 10px;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 105%;
        color: #5A5C69;
        border: 1px solid transparent;
        transition: border-color .3s linear;

        &.error {
            border-color: $orange_color;
            color:  $orange_color;

            &::placeholder {
                color:  $orange_color;
            }
    
        }

        &:hover {
            border-color: #DEDEDE;
        }

        &:focus {
            border-color: $black_color;
            background-color: $white_color;
        }

        &::placeholder {
            color: #5A5C69;
        }

        & ~ .error {
            margin-top: 4px;
            color:  $orange_color;
            font-size: 12px;
            font-style: normal;
            font-weight: 400;
            line-height: 105%; 
        }
    }

    &__col {
        width: calc(50% - 40px);
        margin: 0 20px;
        display: flex;
        flex-direction: column;

        .form__label {
            width: 100%;
            margin: 0;
        }

        @include below(768px) {
            width: 100%;
            margin: 0;

            &:nth-child(2) {
                margin-top: 12px;
            }
        }
    }

    &__add-url {
            color: #212121;
            width: fit-content;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            font-size: 16px;
            font-style: normal;
            font-weight: 500;
            line-height: 24px;
            padding: 8px 30px;
            border-radius: 50px;
            border: 1px solid $black_color;
            transition: background-color .3s linear;

            @include below(768px) {
                margin-top: 12px;
            }

            svg {
                width: 20px;
                height: 20px;
                margin-right: 10px;
            }
    }

    &__social {
        margin: 24px 0 0;

        &-text {
            color: #000;
            font-size: 16px;;
            font-weight: 500;
            line-height: 120%;
            margin-bottom: 16px;
        }

        .form__row {
            .form__col {
                margin-bottom: 10px;
            }
        }
    }

    &__bottom {
        margin-top: 24px;

        .form__row {
            margin: 0 -20px 0;

            @include below(768px) {
                margin: 0;
            }
        }

        &_second {
            margin-top: 40px;
        }
    }

    &__btn {
        width: 100%;
    }

    &__policy {
        margin-top: 24px;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 120%;
        color: #5A5C69;
    }

    &__item {
        display: flex;
        justify-content: space-between;
        font-size: 16px;
        line-height: 120%; 
        color: #5A5C69;

        &::after {
            content: "";
            display: block;
            flex: auto;
            order: 2;
            border-bottom: 1px solid $grey_color;
            margin: 0 10px;
        }

        &-key {
            order: 1;
            font-weight: 400;
        }

        &-val {
            order: 3;
            font-weight: 600;
        }

    }
}