@import "main-bx";

.articles {
    padding: 0 0 100px;

    @include below(768px) {
        padding: 0 0 80px;
    }

    @include below(540px) {
        padding: 30px 0 80px;
    }

    &__tabs-list {
        margin: 0 -3px;
        padding: 0;

        .tabs__item {
            width: 50%;
            margin: 0 3px;
        }
    }

    &__wrap {
        display: flex;

        @include below(1024px) {
            flex-direction: column;
        }
    }

    &__content {
        width: 100%;
        max-width: 1000px;
        margin-right: 20px;

        @include below(1024px) {
            max-width: initial;
            margin: 0 0 40px;
        }

        &-date {
            display: inline-block;
            border-radius: 15px;
            background-color: #F4F4F4;
            padding: 5px 10px;
            font-size: 13px;
            font-weight: 500;
            line-height: normal;
            color: #5A5C69;
            margin: 24px 0;

            @include below(768px) {
                margin: 20px 0 12px;
            }
        }

        &-title {
            font-size: 50px;
            font-weight: 700;
            line-height: 100%;
            text-transform: uppercase;
            margin-bottom: 24px;

            @include below(1340px) {
                font-size: 40px;
            }

            @include below(1240px) {
                font-size: 32px;
            }
            
            @include below(768px) {
                font-size: 26px;
                margin-bottom: 20px;
            }
        }

        &-text {
            font-size: 15px;
            font-weight: 300;
            line-height: 140%;
            color: #5A5C69;

            p {
                &:not(:last-child) {
                    margin-bottom: 10px;
                }
            }
        }
    }

    &__right {
        width: 100%;
        max-width: 380px;

        &-title {
            font-size: 20px;
            font-weight: 600;
            line-height: 110%;
            margin-bottom: 19px;

            @include below(540px) {
                margin-bottom: 12px;
                font-size: 16px;
            }
        }

        .articles__card {
            width: 100%;
            padding: 15px;

            @include below(540px) {
                padding: 20px;
            }

            .articles__body {
                margin-top: 10px;

                @include below(540px) {
                    margin-top: 20px;
                }
            }
        }

        @include below(1380px) {
            max-width: 320px;
        }

        @include below(1240px) {
            max-width: 270px;
        }

        @include below(1024px) {
            max-width: initial;
        }
    }

    &__title {
        font-size: 70px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 40px;

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
            margin-bottom: 20px;
        }

        @include below(540px) {
            font-size: 26px;
        }
    }

    &__main {
        margin-top: 20px;
    }

    &__grid {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin-right: -4px;

        &:not(.is-active) {
            display: none;
        }
    }

    &__card {
        margin-top: -1px;
        margin-right: -1px;
        width: 33.333%;
        display: flex;
        flex-direction: column;
        padding: 20px;
        border: 1px solid #DEDEDE;

        @include below(1240px) {
            width: 50%;
        }

        @include below(600px) {
            width: 100%;
        }
    }

    &__body {
        display: flex;
        flex-direction: column;
        align-items: start;
        flex: auto;
        margin-top: 24px;

        @include below(600px) {
            margin-top: 20px;
        }
    }

    &__body-date {
        border-radius: 15px;
        background-color: #F4F4F4;
        padding: 5px 10px;
        font-size: 13px;
        font-weight: 500;
        line-height: normal;
        color: #5A5C69;
        margin-bottom: 10px;
    }

    &__body-title {
        font-size: 20px;
        font-weight: 600;
        line-height: 110%;
        margin-bottom: 46px;

        @include below(600px) {
            margin-bottom: 42px;
        }

        span {
            text-transform: uppercase;
        }
    }

    &__body-link {
        margin-top: auto;
        svg {
            width: 20px;
            height: 20px;
            margin-left: 10px;
        }
    }

    &__nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 56px;

        @include below(590px) {
            flex-direction: column;
            align-items: start;
            margin-top: 40px;
        }
    }

    &__pagination {
        @include below(590px) {
            width: 100%;
        }
    }

    &__more {
        text-align: center;
        padding: 8px 30px;
        color: $black_color;
        border-radius: 50px;
        border: 1px solid $black_color;
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
        transition: color .3s linear, background-color .3s linear, border-color .3s linear;

        &:hover {
            background-color: #454545;
            color: $white_color;
            border-color: transparent;
        }

        &:active {
            background-color: $black_color;
            color: $white_color;
        }

        @include below(590px) {
            width: 100%;
            margin-bottom: 24px;
        }
    }
}