@import "main-bx";

.counter {
   
    &__form {
        width: 100%;
        display: flex;
        align-items: center;
    }

    &__btn {
        width: 44px;
        height: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50px;
        background-color: #F4F4F4;
        color: #000;
        transition: background-color .3s linear;

        @include below(630px) {
            flex-shrink: 0;
            width: 26px;
            height: 26px;
        }
        svg {
            width: 20px;
            height: 20px;

            @include below(630px) {
                width: 14px;
                height: 14px;
            }
        }

        &:hover:not(:active) {
            background-color: #DEDEDE;
        }
    }

    &__count {
        flex: auto;
        margin: 0 10px;

        @include below(540px) {
            margin: 0 6px;
        }
        
        input {
            width: 100%;
            font-size: 14px;
            font-weight: 400;
            line-height: 100%; 
            color: $black_color;
            text-align: center;
            background-color: #F4F4F4;
            padding: 15px 0;
            border-radius: 50px; 
        
            appearance: none;

            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
              -webkit-appearance: none;
              margin: 0;
            }
            &[type=number] {
                -moz-appearance: textfield;
            }

            
            @include below(630px) {
                padding: 6px 0;
            }
        }
    }

    &__result {
        width: 100%;
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px;

        @include below(540px) {
            flex-direction: column;
            align-items: start;
        }
        &-text {
            font-size: 14px;
            font-weight: 400;
            line-height: 100%;
            color: #5A5C69;

            @include below(540px) {
                font-size: 12px;
                margin-bottom: 4px;
            }
        }

        &-sum {
            color: $orange_color;
            font-size: 24px;
            font-weight: 600;
            line-height: 100%;

            @include below(768px) {
                font-size: 20px;
            }

            @include below(540px) {
                font-size: 16px;
            }
        }
    }
}
