@import "main-bx";

*,
*::before,
*::after {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, ul, ol, p {
    margin: 0;
    padding: 0;
}


.container {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;

    @include below(1380px) {
        max-width: 1200px;
    }

    @include below(1240px) {
        max-width: 1024px;
        padding: 0 25px;
    }

    @include below(1024px) {
        max-width: 768px;
        padding: 0 15px;
    }


    @include below(479px) {
        max-width: 100%;
    }
}

body {
    font-family: $accent_font;
    font-size: 18px;
    line-height: 22px;
    width: 100%;
    // max-width: 1440px;
    margin: 0 auto;
    background-color: $white_color;
    color: $black_color;
    overflow-x: hidden;
    position: relative;
}

a {
    text-decoration: none;
    transition: $trns;
}

input,
button,
textarea,
fieldset {
    background: transparent;
    border: 0;
    padding: 0;
}

button {
    cursor: pointer;
    transition: $trns;
}

img {
    display: block;
    width: 100%;
    height: 100%;
    transition: $trns;
    object-fit: cover;
    max-width: 100%;
}

ul {
    list-style-type: none;
}


:focus {
    outline-style: none;
    outline-width: 0 !important;
    outline-color: none !important;
}

.none {
    display: none;
}