@import "main-bx";

.footer {   
    padding: 0 20px;
    border-top: 1px solid #000;

    @include below(1024px) {
        padding: 0 15px;
        border-top: none;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        padding: 32px 0;
   
     

        @include below(1024px) {
            flex-wrap: wrap;
            border-top: 1px solid #000;
        }

        @include below(650px) {
            padding: 27px 0;
        }

        @include below(540px) {
            padding: 27px 0 0;
        }
    }

    &__col {

        @include below(1380px) {
            &:nth-child(2) {
                flex: auto;
                margin: 0 60px;
                display: flex;
            }
        }

        @include below(1024px) {
            &:nth-child(1) {
                order: 1;
            }

            &:nth-child(2) {
                width: 100%;
                order: 3;
                margin: 40px 0 0;
            }

            &:nth-child(3) {
                order: 2;
               
            }
        }

        .footer__policy {
            @include below(1240px) {
                display: none;
            }
        }
        .footer__copy {
            @include below(540px) {
                display: none;
            }
        }
    }

    &__center {
        display: none;

        @include below(1240px) {
            display: block;
            padding-bottom: 50px;
        }

        @include below(540px) {
            padding: 40px 0 0;
        }
    }


    &__logo {
        display: block;
        color: $black_color;
        margin-bottom: 21px;

        @include below(540px) {
            margin: 0;
        }

        svg {
            width: 50px;
            height: 50px;
        }
    }

    &__copy {
        font-size: 13px;
        font-weight: 500;
        line-height: 100%;
        text-transform: uppercase;
    }

    &__nav {
        display: flex;
    }

    &__list {

        &:last-child {
            margin-left: 176px;

            @include below(1380px) {
                margin-left: 70px;
            }

            @include below(1024px) {
               margin-left: 110px;
            }

            @include below(540px) {
                margin-left: 60px;
            }
        }

        &-link {
            color: $black_color;
            font-size: 13px;
            font-weight: 500;
            line-height: 100%;
            text-transform: uppercase;    
            transition: color .3s linear;

            &:hover:not(:active) {
                color: #454545;
            }
        }
    }

    &__policy {
        margin-top: 40px;

        @include below(1380px) {
            margin: 0;
            margin-left: 100px;
            display: flex;
            flex-direction: column;
        }

        @include below(1240px) {
            flex-direction: row;
            margin-left: 0;
        }

        @include below(768px) {
            flex-direction: column;
        }

        &-link {
            color: rgba(33, 33, 33, 0.50);
            font-size: 13px;
            font-style: normal;
            font-weight: 400;
            line-height: 100%; 

            &:not(:first-child) {
                margin-left: 100px;

                @include below_and_above(1380px, 1241px) {
                    margin: 10px 0 0;
                }

                @include below(1024px) {
                    margin: 0 0 0 40px;
                }

                @include below(768px) {
                    margin: 10px 0 0;
                }
        
            }

        
        }
    }


    &__tel {
        color: $black_color;
        font-size: 32px;
        font-weight: 500;
        line-height: 100%;
        text-transform: uppercase;
        transition: color .3s linear;

        @include below(1024px) {
            font-size: 28px;
        }

        @include below(540px) {
            font-size: 24px;
        }

        &:hover:not(:active) {
            color: #454545;
        }
    }

    &__social {
        margin-top: 30px;
        display: flex;
        justify-content: end;

        @include below(1024px) {
            margin-top: 20px;
        }

        @include below(540px) {
            margin-top: 6px;
        }
    }

    &__social-link {
        color: $black_color;
        transition: color .3s linear;

        &:hover:not(:active) {
            color: #454545;
        }

        &:not(:last-child) {
            margin-right: 8px;
        }

        svg {
            width: 40px;
            height: 40px;

            @include below(1024px) {
                width: 32px;
                height: 32px;
            }
        }
    }

    &__bottom {
        display: none;

        @include below(540px) {
            display: block;
            margin-top: 45px;
            padding: 20px 0;
            border-top: 1px solid #000;
        }
    }
}