@import "main-bx";

.dealer {
    padding: 0 0 100px;

    @include below(768px) {
        padding: 0 0 80px;
    }

    @include below(540px) {
        padding: 30px 0 80px;
    }

    &__main {
        width: 100%;
        max-width: 1050px;
        margin: 0 auto;
    }

    &__title {
        text-align: center;
        font-size: 70px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 40px;

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
            margin-bottom: 20px;
            text-align: start;
        }

        @include below(540px) {
            font-size: 26px;
        }
    }

    &__subtitle {
        text-align: center;
        font-size: 32px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        margin-bottom: 24px;

        
        @include below(1240px) {
            font-size: 28px;
        }

        @include below(1024px) {
            font-size: 24px;
        }

        @include below(768px) {
            font-size: 20px;
            text-align: start;
            margin-bottom: 20px;
        }

        @include below(540px) {
            font-size: 18px;
        }
    }

    &__text {
        font-size: 15px;
        font-weight: 300;
        line-height: 140%;
        color: #5A5C69;

        p {
            &:first-child {
                margin-bottom: 10px;
            }
        }
    }

    &__benefits {
        margin-top: 80px;

        @include below(768px) {
            margin-top: 40px;
        }
    }

    &__cooperation {
        margin: 60px 0;
        background-color: #5A5C69;
        padding: 30px 40px 30px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: $white_color;

        @include below(1024px) {
            padding: 20px;
        }

        @include below(768px) {
            margin: 40px 0;
        }

        @include below(640px) {
            flex-direction: column;
            align-items: start;
        }

        &-text {
            font-size: 24px;
            font-weight: 500;
            line-height: 110%;
            text-transform: uppercase;

            @include below(1024px) {
                font-size: 20px;
            }

            @include below(640px) {
                font-size: 18px;
                margin-bottom: 20px;
            }
        }

        &-tel {
            color: $white_color;
            font-size: 32px;
            font-weight: 600;
            line-height: 100%;
            transition: text-decoration .3s linear;

            
            @include below(1024px) {
                font-size: 26px;
            }

            @include below(768px) {
                font-size: 22px;
            }

            @include below(640px) {
                font-size: 18px;
            }

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__form {
        background-color: #F4F4F4;
        padding: 40px;
 
        @include below(540px) {
            padding: 20px 15px 30px;
        }
    }
}