@import "main-bx";

.fancybox__container {
    .fancybox__backdrop {
        background-color: rgba(0, 0, 0, 0.80);
    }
}

.modal {
    display: none;

    &.fancybox__content {
        display: none;
        padding: 40px 40px 60px;
        width: 100%;
        max-width: 792px;

         
        @include below(540px) {
            padding: 32px 20px 40px;
        }

        .f-button {
            padding: 12px;
            width: 44px;
            height: 44px;
            top: 10px !important;
            right: 10px !important;
            border-radius: 50px;
            border: 1px solid  #DEDEDE;
            background-color: #fff;

            &:hover {
                background-color: #F4F4F4;
            }

            @include below(575px) {
                width: 30px;
                height: 30px;
                padding: 0;
            }

            svg {
                width: 20px;
                height: 20px;
                color: #212121;

                @include below(575px) {
                    width: 14px;
                    height: 14px;
                    color: #212121;
                }
            }
        }
    }

    &__box {
        display: flex;
        flex-direction: column;
        align-items: center;
    }


    &__icon {
        width: 82px;
        min-width: 82px;
        height: 82px;
        margin-bottom: 16px;

        @include below(540px) {
            width: 50px;
            min-width: 50px;
            height: 50px;
        }
    }

    &__title {
        color: #0E0E0E;
        font-size: 40px;
        font-style: normal;
        font-weight: 600;
        line-height: 120%;
        margin-bottom: 12px;

        @include below(768px) {
            font-size: 35px;
        }

        @include below(540px) {
            font-size: 26px;
            margin-bottom: 7px;
        }
    }

    &__text {
        text-align: center;
        color: #0E0E0E;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 110%;
        margin-bottom: 40px;

        
        @include below(540px) {
            margin-bottom: 32px;
        }

        @include below(479px) {
            
            br {
                display: none;
            }
        }
    }

    &__btn  {
        width: 100%;
        max-width: 280px;

        @include below(479px) {
            max-width: initial;
        }
    }
}