@import "main-bx";

.tabs {
    &__list {
        display: flex;
        overflow: hidden;
        overflow-x: auto;
        padding-bottom: 4px;
        @include horizontal-scroll;
    }

    &__item {
        flex: auto;
        &:not(:last-child) {
            margin-right: 6px;
        }
    }

    &__button {
        display: block;
        width: 100%;
        background-color: #F4F4F4;
        padding: 16px 18px;
        white-space: nowrap;
        text-align: center;
        color: $black_color;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 100%; 
        text-transform: uppercase;
        transition: background-color .3s linear, color .3s linear;

        &_active {
            background-color: $orange_color;
            color: $white_color;
        }

        &:hover:not(.tabs__button_active) {
            background-color: #DEDEDE;
        }

        &:active {
            background-color: $orange_color;
            color: $white_color;
        }
    }
}