@import "main-bx";

.discover {

    &__wrap {
        position: relative;
    }

    &__content {
        position: absolute;

        @include below(1024px) {
           position: static;
           padding: 60px 0;
        }

        @include below(575px) {
            position: static;
            padding: 24px 0 60px;
        }

        &_first {
            top: 80px;
            right: 92px;
            display: flex;
            flex-direction: column;
            align-items: end;

            @include below(1024px) {
                align-items: start;
               
            }

            @include below(479px) {
                .discover__title {
                    br {
                        display: block;
                    }
                }
            }

            .discover__text {
                text-align: end;

                @include below(1024px) {
                    text-align: start;
                }
            }

        }

        &_second {
            top: 80px;
            left: 80px;
        }

        &_third {
            right: 50px;
            bottom: 80px;

            .discover__title {
                color: $white_color;

                @include below(1024px) {
                    color: $black_color;
                }
            }
        }

        &_fourth {
            top: 140px;
            left: 250px;

            @include below(1700px) {
                top: 30px;
                left: 100px;
            }

            
            @include below(1024px) {
                padding: 60px 15px;
            }

            @include below(575px) {
                padding: 24px 15px 80px;
            }
        }
    }

    &__title {
        font-size: 70px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
        }

        @include below(540px) {
            font-size: 26px;
        }

        br {
            @include below(1024px) {
                display: none;
            }
        }
    }

    &__text {
        max-width: 400px;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
        text-transform: uppercase;
        margin-top: 32px;

        @include below(1024px) {
            margin-top: 24px;
            max-width: initial;
        }

        @include below(540px) {
            font-size: 12px;
        }
    }

    &__btn {
        margin-top: 32px;

        @include below(1024px) {
            margin-top: 24px;
        }

        @include below(540px) {
            width: 100%;
        }

        &_white {
            border: 1px solid transparent;
            background-color: $white_color;
            color: $black_color;
            transition: background-color .3s linear;

            &:hover:not(:active) {
                background-color: #D0D0D0;
            }

            @include below(1024px) {
                background-color: $black_color;
                color: $white_color;

                &:hover:not(:active) {
                    background-color: #454545;
                }
            }
        }
    }
}