@import "main-bx";

.order {
    padding-bottom: 100px;

    @include below(540px) {
        padding: 40px 0 80px;
    }

    &__head {
        margin-bottom: 60px;
    }

    &__head-flex {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;

        @include below(768px) {
            flex-direction: column-reverse;
            align-items: start;
        }
    }

    &__title {
        font-size: 70px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;

        br {
            display: none;
        }

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
        }

        @include below(540px) {
            font-size: 26px;
            br {
                display: block;
            }
        }
    }

    &__btn {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
        color: #212121;
        border: 1px solid #212121;
        border-radius: 50px;
        padding: 8px 30px;
        transition: background-color .3s linear, color .3s linear;

        &:hover {
            background-color: #454545;
            color: #fff;
            border-color: #454545;

        }

        &:active {
            background-color: #212121;
            color: #fff;
            border-color: #212121;
        }

        svg {
            width: 20px;
            height: 20px;
            margin-right: 13px;
        }

        @include below(768px) {
            margin-bottom: 20px;
        }
    }

    &__head-text {
        color: #5A5C69;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px;

        p:first-child{
            margin-bottom: 12px;
        }
    }


    &__row {
        display: flex;
        margin-bottom: 60px;

        @include below(768px) {
            flex-direction: column;
        }
    }

    &__item {
        padding: 0 32px;
        border-left: 1px solid #A1A1A1;

        @include below(1024px) {
            padding: 0 16px;
        }

        @include below(768px) {
            display: flex;
            justify-content: space-between;
            border: none;
            padding: 0 0 16px;

            &::after {
                content: "";
                display: block;
                flex: auto;
                order: 2;
                border-bottom: 1px solid #5A5C69;
                margin: 0 10px;
            }
        }

        &:first-child {
            padding-left: 0;
            border-left: none;

            @include below(768px) {
                padding-right: 0;
            }
        }

        &:last-child {
            padding-right: 0;

            @include below(768px) {
                padding: 0;
            }
        }
    }

    &__item-name {
        font-size: 16px;
        line-height: 16px;
        color: #5A5C69;
        margin-bottom: 12px;

        @include below(768px) {
           margin-bottom: 0;
           order: 1;
        }
    }

    &__item-val {
        font-size: 20px;
        font-weight: 600;
        line-height: 20px;
        color: #5A5C69;

        @include below(768px) {
            order: 3;
            font-size: 16px;
        }

        a {
            color: #5A5C69;
        }
    }

    &__info {
        margin-bottom: 60px;

        &-title {
            font-weight: 700;
            font-size: 24px;
            line-height: 24px;
            text-transform: uppercase;
            margin-bottom: 20px;

            @include below(1240px) {
                font-size: 20px;
            }

            @include below(600px) {
                font-size: 18px;
            }
        }
    }

    &__list {
        border: 1px solid #F4F4F4;
    }

    &__list-item {
        display: flex;
        align-items: stretch;
        font-size: 16px;
        line-height: 20px;
        border-bottom: 1px solid #F4F4F4;

        @include below(768px) {
            display: flex;
            flex-direction: column;
            align-items: start;
            padding: 15px 20px;
            font-size: 15px;

            @include below(540px) {
                padding: 15px 11px;
            }

            &:first-child {
                .order__list-price {
                    display: none;
                }
            }
        }

        &:first-child,
        &:last-child {
            background-color: #F4F4F4;

            .order__list-product {
                strong {
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 24px;

                    @include below(1240px) {
                        font-size: 15px;
                    }
                }
            }

            .order__list-price {
                strong {
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 24px;

                    @include below(1240px) {
                        font-size: 15px;
                    }
                }
            }
        }

        &:nth-child(2),
        &:nth-child(3) {
            font-weight: 500;
        }

        &:nth-child(4),
        &:nth-child(5) {
            .order__list-product {
                font-weight: 600;
            }

            .order__list-price {
                font-weight: 700;
            }
        }
    }

    &__list-product {
        width: 100%;
        max-width: 800px;
        padding: 16px 20px;

        @include below(1240px) {
            max-width: 500px;
        }

        @include below(768px) {
            padding: 0;
        }

        span {
            display: inline-block;
            padding: 6px 30px;
            border-radius: 50px;
            background-color: #F4F4F4;
            margin-left: 10px;

            @include below(460px) {
                margin: 5px 0 0;
            }
        }
    }

    &__list-price {
        padding: 16px 20px;
        border-left: 1px solid #F4F4F4;

        @include below(768px) {
            padding: 0;
            border: none;
            margin-top: 10px;
        }
    }

    &__address-title {
        font-weight: 700;
        font-size: 24px;
        line-height: 24px;
        text-transform: uppercase;
        margin-bottom: 20px;

        @include below(1240px) {
            font-size: 20px;
        }

        @include below(600px) {
            font-size: 18px;
        }
    }

    &__address-row {
        display: flex;
        align-items: center;
        background-color: #F4F4F4;
        padding: 20px;

        @include below(1024px) {
            flex-direction: column;
            align-items: start;
        }

        @include below(540px) {
            padding: 20px 11px;
        }
    }

    &__address-item {
        display:flex;
        flex-direction: column;

        &:not(:last-child) {
            margin-right: 80px;

            @include below(1024px) {
                margin: 0 0 32px;
            }
        }
    }

    &__address-name {
        font-size: 16px;
        line-height: 16px;
        margin-bottom: 12px;

        @include below(540px) {
            font-size: 15px;
            line-height: 15px;
        }
    }

    &__address-text {
        font-size: 20px;
        font-weight: 600;
        line-height: 20px;

        @include below(540px) {
            font-size: 18px;
            line-height: 18px;
        }

        a {
            color: #212121;
        }
    }
}