@import "main-bx";

.gallery {
    position: relative;
    display: flex;
    align-items: start;
    padding-left: 305px;

    @include below(1240px) {
        padding-left: 200px;
    }

    @include below(1024px) {
        padding-left: 0;
    }

    &__content {
       
        width: 100%;
        max-width: 780px;
        height: 525px;
        padding: 0 40px;

        @include below(1240px) {
            padding: 0;
        }

        @include below(1024px) {
            height: 450px;
        }

        @include below(768px) {
            height: auto 
        }
    }

    &__img {
        height: 100%;
        width: 100%;
        max-width: 700px;

        .cloudimage-360-inner-box {
            height: 100%;

            canvas {
                width: 100% !important;
                height: 100% !important;
                object-fit: cover;
            }

            .cloudimage-360-view-360-icon {
                @include below(768px) {
                    width: 70px;
                    height: 70px;
                }
                
            }
        }
    }

    &__btn {
        padding-top: 55px;
        margin-right: 20px;

        @include below(1240px) {
            padding-top: 30px; 
        }

        @include below(750px) {
            position: absolute;
            padding: 0;
            margin-right: 0;
            top: -30px;
            right: 0;
            z-index: 10;
        }

        svg {
            width: 130px;
            height: 130px;

            @include below(1240px) {
                width: 100px;
                height: 100px;
            }

            @include below(1024px) {
                width: 70px;
                height: 70px;
            }
        }
    }
}
