@import "main-bx";

.btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    border-radius: 50px;
    padding: 8px 30px;
    transition: background-color .3s linear;

    &_red {
        color: $white_color;
        background-color: $orange_color;

        &:hover:not(:active) {
            background-color: #D70403;
        }
    }



    &_black {
        color: $white_color;
        background-color: $black_color;

        &:hover:not(:active) {
            background-color: #454545;
        }
    }

}