@import "main-bx";

.breadcrumbs {
    padding-bottom: 32px;

    @include below(540px) {
        display: none;
    }

    &__list {
        display: flex;
        align-items: center;
    }

    &__item {
        font-size: 13px;
        font-weight: 400;
        line-height: 100%;

        &:not(:last-child) {
            &::after {
                content: " → ";
                display: inline-block;
                color: #5A5C69;
                margin: 0 5px;
            }
        }

        a {
           color: #5A5C69;
        }

        span {
            color: $black_color;
        }
    }
}