@import "main-bx";

.card-detail {
    padding-bottom: 100px;

    @include below(768px) {
        padding-bottom: 80px;
    }

    @include below(540px) {
        padding: 30px 0 80px;
    }

    

    &__suptitle {
        font-size: 18px;
        font-weight: 400;
        line-height: 110%;
        color: #A1A1A1;

        @include below(768px) {
           font-size: 14px;
        }

        @include below(540px) {
            font-size: 12px;
        }
    }

    &__title {
        font-size: 50px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        margin: 12px 0;

        @include below(1340px) {
            font-size: 40px;
        }

        @include below(1240px) {
            font-size: 32px;
        }
        
        @include below(768px) {
            font-size: 26px;
        }
    }

    &__badge {
        display: inline-block;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 100%;
        color: $orange_color;
        padding: 4px 12px;
        background-color: rgba(234, 43, 42, 0.08);
        margin-bottom: 20px;

        @include below(540px) {
           padding: 4px 10px;
           font-size: 12px;
           margin-bottom: 10px;
        }
    }

    &__tabs {
        position: sticky;
        top: 0px;
        background-color: #fff;
        z-index: 100;
        padding: 20px 0 20px;
        margin-bottom: 10px;
        @include below(768px) {
            margin-bottom: 0;
        }
    }

    &__tabs-list {
        .tabs__item {
            width: 20%;

            @include below(1380px) {
               width: fit-content;
            }
        }

    }

    &__main {
        border: 1px solid #DEDEDE;
        padding: 0 30px;

        @include below(768px) {
            padding: 0 15px;
        }
    }

    &__item {
        position: relative;
        padding: 30px 0;

        @include below(540px) {
            padding: 20px 0
        }
        &_flex {
            display: flex;
            justify-content: space-between;

            @include below(1380px) {
                flex-direction: column;
            }
        }

        &:not(:last-child) {
            border-bottom: 1px solid #DEDEDE;
        }

        &-title {
            font-size: 24px;
            font-weight: 500;
            line-height: 100%;

            @include below(1024px) {
                font-size: 20px;
            }

            @include below(540px) {
                font-size: 18px;
            }
        }
    }

    &__descr {
        max-width: 900px;
        font-size: 15px;
        font-weight: 300;
        line-height: 140%;
        color: #5A5C69;

        @include below(1380px) {
            margin-top: 25px;
            max-width: initial;
        }

        @include below(540px) {
            margin-top: 15px;
        }

        p {
            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }
    }

    &__list {
        width: 100%;
        max-width: 900px;

        @include below(1380px) {
            margin-top: 25px;
            max-width: initial;
        }

        @include below(540px) {
            margin-top: 15px;
        }

    }

    &__list-item {
        padding: 12px 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 15px;
        line-height: 140%;

        @include below(1024px) {
            flex-direction: column;
            align-items: start;
        }

        @include below(768px) {
            padding: 15px 11px;
        }

        span {
            font-weight: 300;
            color: #5A5C69;
        }

        strong {
            font-weight: 500;
        }

        &:nth-child(odd) {
            background-color: #F4F4F4;
        }
    }

  
 
}