@import "main-bx";

.favorites {
    padding: 0 0 100px;

    @include below(768px) {
        padding: 0 0 80px;
    }

    @include below(540px) {
        padding: 30px 0 80px;
    }
 
 
    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        @include below(768px) {
            align-items: start;
            margin-bottom: 20px;
        }
    }

    &__title {
        font-size: 70px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
        }

        @include below(540px) {
            font-size: 26px;
        }
    }

    &__text {
        font-size: 32px;
        font-weight: 400;
        line-height: 110%;
        color: #A1A1A1;

        @include below(1240px) {
            font-size: 26px;
        }

        @include below(1024px) {
            font-size: 20px;
        }

        @include below(479px) {
            font-size: 14px;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin-right: -4px;
    }

    &__card {
        width: 25%;
        margin-top: -1px;
        margin-right: -1px;
        @include below(1380px) {
            width: 33.333%;
        }

        
        @include below(1240px) {
            .card__body {
                .card__bottom {
                    flex-direction: column;
                    align-items: start;
                }

                .card__price {
                    margin-bottom: 12px;
                }
            }
        }

        @include below(1024px) {
            width: 50%;

            .card__bottom {
                .card__btn {
                    width: 100%;
                }
            }
        }
    }
}