@import "main-bx";

.technologies {
    padding: 100px 0 120px;

    @include below(1024px) {
        padding: 20px 0 80px;
    }

    @include below(768px) {    
        padding: 0 0 60px;
    }

    &__title {
        text-align: center;
        font-size: 70px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        margin-bottom: 40px;

        br {
            display: none;
        }

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
            text-align: start;
            margin-bottom: 24px;
        }

        @include below(540px) {
            font-size: 26px;
            br {
                display: block;
            }
        }
    }

    &__nav {
        display: flex;
        overflow: hidden;
        overflow-x: auto;
        padding-bottom: 2px;
        margin-bottom: 20px;
    
        @include horizontal-scroll;

        @include below(768px) {
            @include hide-scroll-bar;

            margin-bottom: 24px;
        }
    }

    &__nav-item {
        white-space: nowrap;
        text-align: center;
        flex: auto;
        background-color: #F4F4F4;
        padding: 18px 16px;
        font-size: 12px;
        font-weight: 500;
        line-height: 100%; 
        text-transform: uppercase;
        color: $black_color;
        cursor: pointer;

        &:not(:last-child) {
            margin-right: 6px;
        }

        &:hover {
            background-color: #DEDEDE;
        }
        &.is_active,
        &:active {
            background-color: $orange_color;
            color: $white_color;
        }
    }

    &__wrap {
        position: relative;
    }

    &__btn {
        width: 40px;
        height: 40px;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        z-index: 10;
        color: $white_color;
        transition: color .3s linear;

        @include below(1240px) {
            width: 32px;
            height: 32px;
        }

        @include below(1024px) {
            display: none;
        }

        svg {
            width: 100%;
            height: 100%;
        }

        &_prev {
            left: 20px;

            @include below(1240px) {
                left: 6px;
            }
        }

        &_next {
            right: 51.4%;

            @include below(1240px) {
                right: 50.6%;
            }
        }
    }

    &__slider {
        &.slick-initialized .slick-slide {
            display: flex;
            align-items: stretch;

            @include below(1024px) {
                flex-direction: column-reverse;
            }
        }
    }

    &__item {
        display: flex;

        &:not(:first-of-type) {
            display: none;
        }
    }

    &__content {
        padding: 0 120px;
        width: 50%;
        background-color: $orange_color;
        color: $white_color;
        display: flex;
        flex-direction: column;
        justify-content: center;

        @include below(1240px) {
            padding: 0 50px;
        }

        @include below(1024px) {
            width: 100%;
            padding: 50px;
        }

        
        @include below(590px) {
            padding: 20px 15px;
        }

        &-title {
            font-size: 40px;
            font-weight: 700;
            line-height: 100%; 
            text-transform: uppercase;
            margin-bottom: 32px;

            @include below(1240px) {
                font-size: 30px;
                margin-bottom: 24px;
            }

            @include below(590px) {
                font-size: 22px;
            }
        }

        &-text {
            max-width: 460px;
            font-size: 14px;
            font-weight: 400;
            line-height: 120%;

            @include below(1024px) {
                max-width: initial;
            }
        }
    }

    &__img {
        width: 50%;

        @include below(1024px) {
            width: 100%;
        }
    }
}