@import "main-bx";

.product {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    border: 1px solid #DEDEDE;
    position: relative;

    @include below(1240px) {
        flex-direction: column;
    }

    @include below(1240px) {
        padding: 15px 15px 20px;
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding: 40px 20px;
        width: 100%;
        max-width: 974px;

        @include below(1380px) {
            max-width: 800px;
        }
    
        @include below(1240px) {
            max-width: initial;
            justify-content: stretch;
            padding: 0 30px;
        }

        @include below(1024px) {
           padding: 0;
        }
    }

    &__nav {
        width: 100%;
        max-width: 130px;
        position: relative;
        padding: 100px 0;

        @include below(1024px) {
            display: none;
        }

        &-btn {
            position: absolute;
            left: 50%;
            transform: translate(-50%, 0);
            width: 32px;
            height: 32px;
            color: $black_color;
            transition: color .3s linear;

            &:hover:not(:active) {
                color: $orange_color;
            }

            &:disabled {
                color: #A1A1A1;
            }

            &_prev {
                top: 55px;
            }

            &_next {
                bottom: 60px;
            }
            svg {
                width: 100%;
                height: 100%;
            }
        }


    }

    &__nav-inner {
        display: flex;
        flex-direction: column; 
        width: 100%;

        &.slick-slider .slick-vertical {
            width: 100%;
        }

        &.slick-slider .slick-track  {
            display: flex;
            flex-direction: column;
            width: 100%;
        }

        &.slick-initialized .slick-slide {
            opacity: 0.5;
        }

        &.slick-initialized .slick-slide.slick-current {
            opacity: 1;
        }

        .slick-track {
            height: auto !important;
        }
    }

    &__nav-item {
        width: 100%;
        margin-bottom: 7px;
        &:not(:nth-child(-n+3)) {
            display: none;
        }
    }

    &__slider {
        max-width: 780px;
        padding: 0 40px;

        @include below(1380px) {
            max-width: 600px;
            padding: 60px 40px;
        }

        @include below(1240px) {
            margin-left: 80px;
        }

        @include below(1024px) {
            margin: 0 auto;
            padding: 0;
            max-width: 500px;

            .slick-dots {
                display: flex;
                padding: 30px 0;
                bottom: -50px;
    
                @include below(540px) {
                    bottom: -10px;
                    padding: 10px 0;
                }
            }
    
            .slick-dots button {
                display: none;
            }
            
            .slick-dots li {
                width: 25%;
                height: 1px;
                margin: 0 2px;
                background-color: #A1A1A1;
                transition: background-color .3s linear;
    
    
                &.slick-active {
                    background-color: $orange_color;
                }
            }
        }

        @include below(570px) {
            max-width: 400px;
        }

        @include below(470px) {
            max-width: 315px;
        }

        @include below(370px) {
            max-width: 280px;
        }

        &-item {
            &:not(:first-of-type) {
                display: none;
            }
        }
    }

    &__content {
        margin-top: 0;
        width: 100%;
        max-width: 427px;
        padding: 30px;
        border-left: 1px solid #DEDEDE;

        
        @include below(1240px) {
            max-width: initial;
            border-left: none;
            border-top: 1px solid #DEDEDE;
        }

        @include below(1024px) {
           padding: 30px 0 0;
           border-top: none;
        }

        @include below(570px) {
            padding: 0;
        }
    }

    &__top {
        width: 100%;
        position: static;
        display: flex;
        align-items: start;
        margin-bottom: 20px;

        @include below(768px) {
            flex-direction: row;
        }

        @include below(570px) {
            width: initial;
            position: absolute;
            top: 10px;
            left: 10px;
            right: 10px;
            margin-bottom: 0;
            flex-direction: row-reverse;
        }

        &-btn {
            width: 64px;
            height: 64px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50px;
            border: 1px solid #DEDEDE;
            background-color: $white_color;
            color: $black_color;
            transition: background-color .3s linear;
            &:first-of-type {
                margin-right: 6px;

                @include below(540px) {
                    margin-right: 0;
                }
            }
    
            @include below(640px) {
                width: 36px;
                height: 36px;
            }
    
            @include below(540px) {
                width: 26px;
                height: 26px;
            }
    
            &:hover {
                background-color: #F4F4F4;
            }
    
            &:active {
                border-color: transparent;
                background-color: $orange_color;
                color: $white_color;
            }
    
            svg {
                width: 24px;
                height: 24px;
                transition: color .3s linear;
    
                @include below(640px) {
                    width: 16px;
                    height: 16px;
                }
    
                @include below(540px) {
                    width: 14px;
                    height: 14px;
                }
            }
    
        }
    }
  
    &__buttons {
        display: flex;

        @include below(570px) {
            flex-direction: column;
        }
    }

    &__badge {
        display: inline-block;
        margin-left: auto;
        font-size: 13px;
        font-weight: 500;
        line-height: normal;
        color: #34A852;
        padding: 4px 10px;
        background-color: #EBF7EE;
        border-radius: 15px;

        @include below(570px) {
            font-size: 12px;
            margin-left: 0;
            margin-right: auto;
        }
    }

    &__title {
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: 100%;
    }

    &__list {
        margin: 20px 0 24px;

        &-item {
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 120%;
            display: flex;
            align-items: center;
            &:not(:last-child) {
                margin-bottom: 16px;
            }

            &::after {
                content: "";
                display: block;
                border-bottom: 1px solid #F4F4F4;
                flex: auto;
                order: 2;
                margin: 0 10px;
            }
        }

        &-key {
            order: 1;
            color: #5A5C69;
        }

        &-val {
            order: 3;
        }
    }

    &__flex {
        position: relative;
        width: 100%;
        display: flex;
        align-items: end;
        margin-bottom: 24px;

        @include below(570px) {
            flex-direction: column;
            margin-bottom: 40px;
        }

        &-left {
            display: flex;
            flex-direction: column;
            align-items: start;
            margin-right: auto;

            @include below(570px) {
                align-items: end;
                margin: 0;
            }
        }

        &-badge {
            font-size: 16px;
            font-weight: 400;
            line-height: normal;
            color: $red_color;
            padding: 2px 4px 1px 2px;
            background-color: #FDE9E9;
            display: inline-block;
            border-radius: 20px;
            margin-bottom: 8px;
        }

        del {
            margin-top: 8px;
            font-size: 16px;
            font-weight: 400;
            line-height: 100%;
            color: #5A5C69;

            @include below(570px) {
                margin: 0;
            }
        }
     
    }

    &__price {
        font-size: 32px;
        font-style: normal;
        font-weight: 400;
        line-height: 100%; 
        color: $orange_color;

        @include below(570px) {
            position: absolute;
            left: 0;
            top: 10px;
        }
    }

    &__sizes {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 25px;

        @include below(1240px) {
            justify-content: stretch;
        }

        @include below(479px) {
            justify-content: space-between;
        }

        &-text {
            font-size: 16px;
            color: #A1A1A1;

            @include below(1240px) {
                margin-right: 16px;
            }

            @include below(479px) {
                margin-right: 0;
            }
        }

        &-select {
            width: 100%;
            max-width: 246px;
            border: 1px solid #F4F4F4;

            & ~ .select2-container {
                width: 100%;
                max-width: 246px;
                .select2-selection--single {
                    width: 100%;
                    max-width: 246px;
                    border: 1px solid #F4F4F4;
                }
            }

            @include below(479px) {
                max-width: 194px;

                & ~ .select2-container {
                    max-width: 194px;
                    .select2-selection--single {
                        max-width: 194px;
                    }
                }
            }
        }
    }

    &__features {
        display: flex;
        flex-wrap: wrap;
        margin: -2px -1px 0;

        &-title {
            display: block;
            font-size: 20px;
            font-style: normal;
            font-weight: 500;
            line-height: 100%;
            margin-bottom: 20px;
        }

        &-item {
            margin: 2px 1px 0;
            font-size: 14px;
            font-weight: 400;
            line-height: normal;
            color: #000;
            padding: 5px 10px;
            border-radius: 15px;
            border: 1px solid  #EAEAEA;
        }
    }

    &__btn {
        svg {
            margin-right: 10px;
            width: 20px;
            height: 20px;
        }
        @include below(570px) {
            width: 100%;
            margin-top: 20px;
        }
    }
}