@import "main-bx";

.catalog {
    padding: 60px 0 120px;

    &.catalog_second {
        padding: 0 0 100px;

        @include below(768px) {
            padding: 0 0 80px;
        }

        @include below(540px) {
            padding: 30px 0 50px;
        }

    }

    @include below(768px) {
        padding: 60px 0 60px;
    }

    @include below(479px) {
        padding: 95px 0 60px;
    }

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;

        @include below(768px) {
            align-items: start;
            margin-bottom: 32px;
        }
    }

    &__text {
        font-size: 32px;
        font-style: normal;
        font-weight: 400;
        line-height: 110%; 
        color: #A1A1A1;

        @include below(1240px) {
            font-size: 26px;
        }

        @include below(1024px) {
            font-size: 20px;
        }

        @include below(479px) {
            font-size: 14px;
        }
    }

    &__title {
        font-size: 70px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;

        &_second {
            br {
                display: none;

                @include below(768px) {
                    display: block;
                }
            }
        }

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
        }

        @include below(540px) {
            font-size: 26px;
        }
    }


    &__row {
        margin-top: 77px;
        display: flex;
        flex-wrap: wrap;

        @include below(1340px) {
            margin-top: 50px;
        }

        @include below(1024px) {
            margin-top: 30px;
        }

        @include below(540px) {
            margin-top: 20px;
        }
    }

    &__item {
        width: 33.333%;
        position: relative;

        @include below(1024px) {
            width: 50%;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.30);
            transition: background-color .3s linear;
        }

        &:hover {
            &::before {
                background-color: $orange_color;
            }

            .catalog__item-icon {
                opacity: 1;
            }
        }
    }

    &__item-name {
        position: absolute;
        left: 50%;
        bottom: 40px;
        transform: translate(-50%, 0);
        text-align: center;
        font-size: 24px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        color: $white_color;

        @include below(1240px) {
            font-size: 20px;
            bottom: 33px;
        }

        @include below(650px) {
            font-size: 16px;
        }
    }

    &__item-icon {
        position: absolute;
        top: 43px;
        left: 50%;
        transform: translate(-50%, 0);
        z-index: 5;
        opacity: 0;
        transition: opacity .3s linear;

        @include below(1380px) {
            top: 20px;
        }

        @include below(575px) {
            top: 80px;
        }

        @include below(440px) {
            top: 40px;
        }


        svg {
            width: 320px;
            height: 211px;

            @include below(1380px) {
                width: 290px;
                height: 180px;
            }

            @include below(1240px) {
                width: 250px;
                height: 140px;
            }

            @include below(600px) {
                width: 140px;
                height: 100px;
            }
        }
    }

    &__wrap {
        margin-top: 120px;
        position: relative;

        @include below(1024px) {
            margin-top: 80px;
        }

        @include below(768px) {
            margin-top: 60px;
        }
    }

    &__slider {
        &.slick-initialized .slick-slide {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    &__slider-item {
        display: flex;
        flex-direction: column;
        align-items: center;

        &:not(:first-of-type) {
            display: none;
        }
    }

    &__slider-title {
        font-size: 50px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        margin-bottom: 20px;

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(1024px) {
            font-size: 30px;
        }

        @include below(650px) {
            font-size: 20px;
            margin-bottom: 32px;
        }
    }

    &__slider-img {
        max-width: 900px;

        @include below(1380px) {
            max-width: 800px;
        }

        @include below(1240px) {
            max-width: 700px;
        }

        @include below(1024px) {
            max-width: 550px;
        }

        @include below(650px) {
            max-width: 450px;
        }
    }

    &__wrap-btn {
        display: block;
        width: 100%;
        max-width: 144px;
        margin: 20px auto 0;

        @include below(575px) {
            margin: 32px auto 0;
        }
    }

    &__slider-btn {
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        color: $black_color;
        width: 40px;
        height: 40px;
        z-index: 10;
        transition: color .3s linear;

        @include below(575px) {
            top: 94%;
        }

        &:hover:not(:active) {
            color: $orange_color;
        }

        &:disabled {
            color: #A1A1A1;
        }

        &_prev {
            left: 40px;

            @include below(1240px) {
                left: 0;
            }
        }

        &_next {
            right: 40px;

            @include below(1240px) {
                right: 0;
            }
        }

        svg {
            width: 100%;
            height: 100%;
        }
    }
}