@import "main-bx";

.card {
    padding: 30px 40px 40px;
    border: 1px solid #DEDEDE;
    display: flex;
    flex-direction: column;
    position: relative;

    @include below(1024px) {
        padding: 20px 15px 15px;
    }

    &__vendor-code {
        font-size: 14px;
        font-weight: 400;
        line-height: 100%; 
        color: $grey_color;
        display: inline-block;
        margin-bottom: 16px;

        @include below(540px) {
            font-size: 10px;
            margin-bottom: 20px;
        }
    }

    &__top {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 10;
        display: flex;

        @include below(768px) {
            top: 5px;
            right: 5px;
            flex-direction: column;
        }
    }

    &__top-btn {
        width: 44px;
        height: 44px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50px;
        border: 1px solid #DEDEDE;
        color: $black_color;
        background-color: $white_color;
        transition: background-color .3s linear;

        @include below(640px) {
            width: 36px;
            height: 36px;
        }

        @include below(540px) {
            width: 26px;
            height: 26px;
        }

        &:hover {
            background-color: #F4F4F4;
        }

        &:active {
            border-color: transparent;
            background-color: $orange_color;
            color: $white_color;
        }

        svg {
            width: 20px;
            height: 20px;
            transition: color .3s linear;

            @include below(640px) {
                width: 16px;
                height: 16px;
            }

            @include below(540px) {
                width: 14px;
                height: 14px;
            }
        }

        &_active {
            border-color: transparent;
            background-color:  $orange_color;
            color: $white_color;

            &:hover {
                background-color: #F4F4F4;
                color:  $black_color;
            }
        }
    }

    &__clear {
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        display: flex;
        align-items: center;
        border-radius: 50px;
        border: 1px solid  $black_color;
        padding: 8px 15px;

        svg {
            width: 20px;
            height: 20px;
            margin-right: 10px;
        }
    }

    &__head {

        .slick-track {
            margin-bottom: 25px;

            @include below(540px) {
                margin-bottom: 10px;
            }
        }

        &.slick-dotted.slick-slider {
            margin-bottom: 0;
        }

        .slick-dots {
            display: flex;
            padding: 30px 0;
            bottom: -20px;

            @include below(540px) {
                bottom: -10px;
                padding: 10px 0;
            }
        }

        .slick-dots button {
            display: none;
        }
        
        .slick-dots li {
            width: 33.333%;
            height: 1px;
            margin: 0 2px;
            background-color: #A1A1A1;
            transition: background-color .3s linear;
            z-index: 11;


            &.slick-active {
                background-color:  $orange_color;;
            }
        }

        &-item {
            &:not(:first-of-type) {
                display: none;
            }
        }
    }

    &__body {
        flex: auto;
        margin-top: 24px;
        display: flex;
        flex-direction: column;
        align-items: start;

        @include below(540px) {
           margin-top: 20px;
        }
    }

    &__title {
        font-size: 20px;
        font-weight: 600;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 8px;

        @include below(540px) {
            font-size: 14px;
        }
    }

    &__badge {
        color:  $orange_color;
        font-size: 14px;
        font-weight: 400;
        line-height: 100%;
        background: rgba(234, 43, 42, 0.08);
        padding: 4px 12px;

        @include below(540px) {
            font-size: 12px;
            padding: 4px 10px;
        }
    }

    &__list {
        margin: 16px 0 24px;
        width: 100%;

        @include below(540px) {
            margin: 16px 0 20px;

            &-item {
                display: none;
            }
        }

        &-item {
            display: flex;
            justify-content: space-between;
            font-size: 12px;
            font-weight: 400;
            line-height: 120%;

            &::after {
                content: "";
                display: block;
                border-bottom: 1px solid #F4F4F4;
                flex: auto;
                order: 2;
                margin: 0 10px;
            }

            &:not(:last-child) {
                margin-bottom: 6px;
            }

            &:last-child {
                display: none;

                @include below(540px) {
                    display: block;
                    font-size: 10px;

                    &::after {
                        display: none;
                    }
              
                    -webkit-line-clamp: 3; 
                    display: -webkit-box; 
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }
            }
        }

        &-key {
            order: 1;
            color: #5A5C69;
        }

        &-val {
            order: 3;
        }
    }

    &__sizes {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 25px;

        @include below(1240px) {
            flex-direction: column;
            align-items: start;
        }

        &-text {
            font-size: 12px;
            color: #5A5C69;

            @include below(1240px) {
                margin-bottom: 4px;
            }

            @include below(540px) {
                font-size: 10px;
            }
        }

        &-select {
            width: 100%;
            max-width: 175px;
            border: 1px solid #F4F4F4;

            & ~ .select2-container {
                width: 100%;
                max-width: 175px;
                .select2-selection--single {
                    width: 100%;
                    max-width: 175px;
                    border: 1px solid #F4F4F4;
                }
            }

            @include below(1024px) {
                max-width: initial;

                & ~ .select2-container {
                    max-width: initial;
                    .select2-selection--single {
                        max-width: initial;
                    }
                }
            }
        }
    }

    &__bottom {
        width: 100%;
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &-text {
            font-size: 14px;
            font-weight: 400;
            line-height: 100%;
            color: #5A5C69;
        }
    }

    &__price {
        color: $orange_color;
        font-size: 24px;
        font-weight: 400;
        line-height: 100%;

        @include below(540px) {
            font-size: 20px;
        }

        &_black {
            color: $black_color;
            margin-bottom: 10px;

            @include below(540px) {
                font-size: 16px;
                margin-bottom: 6px;
            }
        }
    }

    &__btn {
        padding: 8px 18px;
        svg {
            width: 20px;
            height: 20px;
            margin-right: 10px;

            @include below(479px) {
                margin-right: 5px;
            }
        }
    }

    &__center {
        width: 100%;
        padding-top: 20px;
        border-top: 1px solid #F4F4F4;
    }
}