@import "main-bx";

.colors {
    display: flex;
    margin: 0 0 24px;

    &__item {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        position: relative;
        &:not(:last-child) {
            margin-right: 10px;
        }
        cursor: pointer;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            border-radius: 50%;
            width: 40px;
            height: 40px;
            transform: translate(-50%, -50%);
            border: 1px solid transparent;
            opacity: 0;
            transition: border-color .3s linear;
        }

        &:hover {
            &::before {
                opacity: 1;
                border-color: #DEDEDE;
            }
        }

        &:active {
            &::before {
                opacity: 1;
                border-color: $black_color;
            }
        }

        &_active {
            &::before {
                opacity: 1;
                border-color: $black_color;
            }
        }

        &[data-color="red"] {
            background-color: $orange_color;
        }

        &[data-color="grey"] {
            background-color: #A1A1A1;
        }

        &[data-color="black"] {
            background-color: $black_color;
        }
    }
}
