@import "main-bx";

.accordion {

    &__item {
        border-top: 1px solid  #DEDEDE;

        &:last-child {
            border-bottom: 1px solid #DEDEDE;
        }
    }

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 32px 0;
        cursor: pointer;

        @include below(768px) {
            padding: 18px 0;
        }

        @include below(540px) {
            align-items: end;
        }
       
        &_active {
            border-bottom: 1px solid #DEDEDE;
            .accordion__btn {
                background-color: $black_color;
                color: $white_color;
                border-color: transparent;

                .close {
                    display: block;
                }

                .open {
                    display: none;
                }
            } 

            & ~ .accordion__inner {
                margin-bottom: 32px;
            }
        }
    }

    &__img {
        max-width: 201px;
        flex-shrink: 0;

        @include below(430px) {
            max-width: 132px;
        }
    }

    &__left {
        display: flex;
        align-items: center;

        @include below(540px) {
            align-items: start;
        }
    }

    &__title {
        display: flex;
        flex-direction: column;
        margin-left: 32px;

        @include below(1024px) {
            margin-left: 20px;
        }

        strong {
            font-size: 40px;
            font-weight: 700;
            line-height: 100%;
            text-transform: uppercase;

            @include below(1240px) {
                font-size: 35px;
            }

            @include below(1024px) {
                font-size: 28px;
            }

            @include below(768px) {
                font-size: 24px;
            }

            @include below(540px) {
                font-size: 20px;
            }
        }

        span {
            display: none;
            font-size: 20px;
            font-weight: 400;
            line-height: 110%;
            color: #A1A1A1;

            @include below(768px) {
                display: block;
                margin-top: 8px;
            }

            @include below(540px) {
                font-size: 14px;
            }
        }
    }

    &__right {
        display: flex;
        align-items: center;

        &-text {
            font-size: 24px;
            font-weight: 400;
            line-height: 110%;
            color: #A1A1A1;

            @include below(1024px) {
                font-size: 20px;
            }

            @include below(768px) {
                display: none;
            }
        }
    }

    &__btn {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 32px;
        color: $orange_color;
        border-radius: 50%;
        border: 1px solid $orange_color;

        @include below(1024px) {
            margin-left: 20px;
        }

        @include below(768px) {
            margin-left: 0;
        }

        &:hover {
            background-color: $orange_color;;
            color: $white_color;
        }
        svg {
            width: 24px;
            height: 24px;
            transition: display .3s linear;
        }

        .close {
            display: none;
        }
    }

    &__inner {
        max-height: 0;
        overflow: hidden;
        opacity: 0;
        transition: max-height .3s linear, opacity .3s linear, overflow .3s linear;
    }

    &__cards {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin-right: -4px;
    }

    &__card {
        width: 25%;

        margin-top: -1px;
        margin-right: -1px;

        @include below(1380px) {
            width: 33.333%;
        }

        @include below(1240px) {
            .card__body {
                .card__bottom {
                    flex-direction: column;
                    align-items: start;
                }

                .card__price {
                    margin-bottom: 12px;
                }
            }
        }

        @include below(1024px) {
            width: 50%;

            .card__bottom {
                .card__btn {
                    width: 100%;
                }
            }
        }

    }

}