@import "main-bx";

.comparison {
    padding-bottom: 100px;

    @include below(768px) {
        padding: 0 0 80px;
    }

    @include below(540px) {
        padding: 30px 0 80px;
    }

    &__head {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
        align-items: end;

        @include below(1024px) {
           align-items: start;
        }

        @include below(768px) {
            margin-bottom: 24px;
        }
    }

    &__title {
        font-size: 70px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
       
        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
        }

        @include below(540px) {
            font-size: 26px;
        }
    }

    &__text {
        display: none;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 110%; 
        color: #A1A1A1;

        @include below(1024px) {
            display: block;
        }

        @include below(768px) {
            font-size: 18px;
        }

        @include below(540px) {
            font-size: 14px;
        }
    }

    &__nav {
        position: relative;
        z-index: 10;

        @include below(1024px) {
            display: none;
        }
    }

    &__nav-btn {
        width: 32px;
        height: 32px;
        color: $black_color;
        transition: color .3s linear;

        &:hover:not(:active) {
            color: $orange_color;
        }
        &:first-of-type {
            margin-right: 16px;
        }
        svg {
            width: 100%;
            height: 100%;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
    }

    &__clear {
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        display: none;
        align-items: center;
        justify-content: center;
        border-radius: 50px;
        color: $black_color;
        border: 1px solid  $black_color;
        padding: 8px 15px;
        transition: color .3s linear, background-color .3s linear;

        @include below(1024px) {
            display: flex;
            margin-bottom: 70px;
        }

        @include below(540px) {
            width: 100%;
        }

        &:hover {
            color: $white_color;
            background-color: $black_color;
        }

        svg {
            width: 20px;
            height: 20px;
            margin-right: 10px;
        }
    }

    &__wrap {
        position: relative;   
        border: 1px solid #DEDEDE; 
    }

    &__card {
       
        &.card {
            padding: 0;
            
            @include below(1024px) {
                border: none;
            }

            .card__cap {
                height: 100%;
                padding: 30px 40px;
                display: flex;
                flex-direction: column;
                align-items: start;
                @include below(1024px) {
                    padding: 20px 15px 15px;
                    border: 1px solid #DEDEDE;
                    border-top: none;
                }

                .card__head {
                    width: 100%;
                    margin-bottom: 30px;

                    @include below(1024px) {
                        margin-bottom: 20px;
                    }
                }
            }

            .card__body {
                margin-top: auto;
                flex: none;
            }

            .card__vendor-code {
                margin: 0;
            }

            .card__btn {
                width: 100%;
            }

            .card__col {
                border-top: 1px solid #F4F4F4;
                @include below(1024px) {
                    border-top: none;
                }

                &-item {
                    font-size: 16px;
                    font-weight: 400;
                    line-height: normal;
                    padding: 12px 40px;
                    border-bottom: 1px solid #F4F4F4;
                    height: 45px;
                    min-height: 45px;
                    &:last-child {
                        border-bottom: none;
                    }

                    &:nth-child(7),
                    &:nth-child(8) {
                        height: 66px;
                        min-height: 66px;

                        @include below(1024px) {
                            height: 80px;
                            min-height: 80px;   
                        }

                        @include below(480px) {
                            display: none;
                        }
                    }

                    &:nth-child(10) {
                        padding: 20px 40px;
                        border: none;
                        height: 80px;
                        min-height: 80px;

                        @include below(1024px) {
                            padding: 20px 15px 15px;   
                        }
                    }

                    strong {
                        font-weight: 600;
                    }

                    @include below(1024px) {
                        padding: 34px 15px 10px;   
                        height: 65px;
                        min-height: 65px;  
                    }

                    @include below(540px) {
                        font-size: 14px;
                    }
       
                    @include below(430px) {
                        padding: 26px 15px 10px;
                    }
                }
            }
        }

    }
    
    &__fixed {
        position: relative;


        // @include below(650px) {
        //     height: 340px;
        // }

        // @include below(600px) {
        //     height: 320px;
        // }

        // @include below(540px) {
        //     height: 265px;
        // }

        // @include below(500px) {
        //     height: 250px;
        // }

        // @include below(450px) {
        //     height: 230px;
        // }

        // @include below(390px) {
        //     height: 205px;
        // }
    }

    &__inner {
        display: flex;
        align-items: stretch;
        width: 100%;

        &.is_fixed {
            overflow: hidden;
            position: fixed;
            top: 0;
            width: 100%;
            max-width: 1400px;
            background-color: $white_color;
            z-index: 11;
            border: 1px solid #DEDEDE;

            @include below(1380px) {
                max-width: 1200px;
            }

            @include below(1240px) {
                max-width: 974px;
            }

            @include below(1024px) {
                max-width: 738px;
            }

            @include below(768px) {
                top: 0;
                left: 0;
                right: 0;
                max-width: 100%;
            }

            .comparison__clear,
            .comparison__item-close,
            .comparison__item-head {
                display: none;
            }

            .comparison__item {
                @include below(768px) {
                    border-left: none;
                }
            }

            .comparison__col {
                position: relative;
                z-index: 30;

                &_first {
                    padding: 55px 20px 52px;
                }

                @include below(768px) {
                    border-left: 1px solid #DEDEDE;
                }

                &_full:not(.comparison__col_clone) {
                    @include below(768px) {
                        padding-left: 15px;
                    }
                }
            }

            .comparison__col-nav {
                @include below(1024px) {
                    top: 10px;
                    z-index: 100;
                }

            }

        }
    }

    &__col {
        &-title {
            font-size: 20px;
            font-style: normal;
            font-weight: 600;
            line-height: 100%; 
        }
        &_first {
            width: 100%;
            max-width: 350px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: start;
            padding: 20px 20px 52px;

            .comparison__clear {
                display: flex;
            }

            @include below(1380px) {
                max-width: 400px;
            }

            @include below(1240px) {
                max-width: 300px;
            }

            @include below(1024px) {
                display: none;
            }
        }

        &_small {
            width: 100%;
            max-width: 350px;

            @include below(1380px) {
                max-width: 400px;
            }

            @include below(1240px) {
                max-width: 300px;
            }

            @include below(1024px) {
                max-width: 100%;
            }
        }

        &_clone {
            display: none !important;

            @include below(1024px) {
                display: flex !important;
            }
        }

        &_full {
            width: 100%;
            max-width: 1050px;
            display: flex;
            position: relative;

            @include below(1380px) {
                max-width: 800px;
            }

            @include below(1240px) {
                max-width: 700px;
            }

            @include below(1024px) {
                max-width: 50%;
            }

        }
    }

    &__col-nav {
        display: none;
        align-items: center;
        justify-content: center;

        @include below(1024px) {
            display: flex;
            position: absolute;
            top: -40px;
            left: 50%;
            transform: translate(-50%,0);
        }

        @include below(640px) {
            top: -30px;
        }

        &-btn {
            width: 30px;
            height: 30px;
            color: $black_color;
            @include below(640px) {
                width: 20px;
                height: 20px;
            }
            
            &:hover:not(:active) {
                color: $orange_color;
            }

            svg {
                width: 100%;
                height: 100%;
            }
        }
    }

    &__counter {
        margin:  0 16px;

        &-numbers {
            white-space: nowrap;
            font-size: 18px;
            font-weight: 400;
            line-height: 110%;

            @include below(640px) {
                font-size: 14px;
            }

            .total {
                color: #A1A1A1;
            }
        }
    }

    &__fix-slider {
        width: 100%;
        display: flex;
        .slick-track {
            display: flex;
            align-items: stretch;
        }

        .slick-slide {
            height: initial !important;
        }

        &.slick-dotted.slick-slider {
            margin-bottom: 0;
        }
    }

    &__item {
        padding: 30px 40px;
        position: relative;
        border-left: 1px solid #DEDEDE;
        border-bottom: 1px solid #DEDEDE;

        &:not(:nth-child(-n+3)):not(.slick-slide) {
            display: none;
        }

        @include below(1380px) {
            &:not(:nth-child(-n+2)):not(.slick-slide) {
                display: none;
            }
        }

        @include below(1024px) {
            &:not(:first-child):not(.slick-slide) {
                display: none;
            }
        }

        @include below(768px) {
            padding: 20px 15px 15px;
        }

        &-close {
            position: absolute;
            top: 10px;
            right: 10px;
            width: 44px;
            height: 44px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #fff;
            color: $black_color;
            border-radius: 50px;
            border: 1px solid  #DEDEDE;
            transition: color .3s linear, background-color .3s linear, color .3s linear;
            z-index: 5;

            @include below(640px) {
                width: 36px;
                height: 36px;
            }
    
            @include below(540px) {
                top: 5px;
                right: 5px;
                width: 26px;
                height: 26px;
            }
            
            &:hover {
                background-color: #F4F4F4;
            }

            &:active {
                background-color: $orange_color;
                color: $white_color;
                border-color: transparent;
            }
            svg {
                width: 20px;
                height: 20px;

                
            @include below(640px) {
                width: 16px;
                height: 16px;
            }

            @include below(540px) {
                width: 14px;
                height: 14px;
            }
            }
        }

        &-title {
            font-size: 20px;
            font-style: normal;
            font-weight: 600;
            line-height: 100%; 
            text-transform: uppercase;
            margin: 30px 0 8px;

            @include below(768px) {
                font-size: 18px;
            }

            @include below(540px) {
                font-size: 14px;
                margin: 20px 0 8px;
            }
        }

        &-text {
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 100%;
            color: #A1A1A1;

            @include below(540px) {
                font-size: 10px;
            }
        }
    }

    &__item-head {
        display: flex;
        .slick-track {
            margin-bottom: 25px;

            @include below(540px) {
                margin-bottom: 10px;
            }
        }

        &.slick-dotted.slick-slider {
            margin-bottom: 0;
        }

        .slick-dots {
            display: flex;
            padding: 30px 0;
            bottom: -20px;

            @include below(540px) {
                bottom: -10px;
                padding: 10px 0;
            }
        }

        .slick-dots button {
            display: none;
        }
        
        .slick-dots li {
            width: 33.333%;
            height: 1px;
            margin: 0 2px;
            background-color: #A1A1A1;
            transition: background-color .3s linear;
            z-index: 11;


            &.slick-active {
                background-color: $orange_color;
            }
        }

    }
  
    &__item-slide {
        &:not(:first-of-type) {
            display: none;
        }
    }

    &__params-row {
        display: flex;
        border-bottom: 1px solid #F4F4F4;

        @include below(1024px) {
            flex-wrap: wrap;
        }

        &:last-child {
            border: none;
        }
    }

    &__params-slider {
        width: 100%;
        display: flex;
    
        .slick-track {
            display: flex;
            align-items: stretch;
        }

        .slick-slide {
            height: initial !important;
        }
    }

    &__param {
        width: 100%;
        padding: 12px 40px;
        border-left: 1px solid #DEDEDE;

        &:not(:nth-child(-n+3)):not(.slick-slide) {
            display: none;
        }

        @include below(1380px) {
            &:not(:nth-child(-n+2)):not(.slick-slide) {
                display: none;
            }
        }

        @include below(1024px) {
            border-left: none;
            &:not(:first-child):not(.slick-slide) {
                display: none;
            }
        }

        @include below(768px) {
            padding: 0 15px 10px;
        }
 

        &-label {
            padding: 12px 20px;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            color: #A1A1A1;

            @include below(1024px) {
                padding: 12px 40px;
            }

            @include below(768px) {
                padding: 10px 15px 6px;
            }

            @include below(540px) {
                font-size: 14px;
            }
        }


        &-val {
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;

            @include below(540px) {
                font-size: 14px;
            }

            strong {
                font-weight: 600;
            }
        }

        &-btn {
            width: 100%;
            svg {
                width: 20px;
                height: 20px;
                margin-right: 10px;
            }
        }
    }
}