@import "main-bx";

.header {
    position: sticky;
    top: 0;
    width: 100%;
    padding: 20px;
    z-index: 200;
    transition: background-color .3s linear;
    &_main {
        margin-bottom: -100px;

        @include below(1024px) {
            margin-bottom: -90px;
        }
        
        @include below(540px) {
            margin-bottom: -55px;
        }
    }

    &.is-fixed {
        background-color: rgba(255, 255, 255, 0.80);
        box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.04);
        backdrop-filter: blur(16px);
        transition: background-color .3s linear;

        .header__menu {
            background-color: rgba(255, 255, 255, 0.80);
            box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.04);
            backdrop-filter: blur(16px);
        }
        
        .header__logo {
            color: $orange_color;
        }

        .header__list {
            &::after,
            &::before {
                background-color: $orange_color;;
            }

            &-link {
                color: $orange_color;

                &:hover,
                &:active {
                    color: #D70403;
                }
            }
        }

        .header__right {
            .header__link {
                color: $orange_color;
                position: relative;

                &:hover,
                &:active {
                    color: #D70403;
                }

                &-numb {
                    position: absolute;
                    right: -4px;
                    bottom: -3px;
                    width: 16px;
                    height: 16px;
                    text-align: center;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50px;
                    background-color: $orange_color;
                    color: $white_color;
                    font-size: 9px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 100%;

                    @include below(768px) {
                        right: -5px;
                        bottom: -5px;
                    }
                }
            }
        }

        .header__burger {
            span {
                background-color: $orange_color;
            }
        }
    }
    
    &_static {
        position: static;

        &.is-fixed {
            position: static;
        }
        
        .header__logo {
            color: $orange_color;
        }

        .header__list {
            &::after,
            &::before {
                background-color:$orange_color;
            }

            &-link {
                color: $orange_color;

                &:hover,
                &:active {
                    color: #D70403;
                }
            }
        }

        .header__right {
            .header__link {
                color: $orange_color;
                position: relative;

                &:hover,
                &:active {
                    color: #D70403;
                }
            }
        }

        .header__burger {
            span {
                background-color: $orange_color;
            }
        }
    }
    
    @include below(1024px) {
        padding: 20px  15px;
    }

    @include below(540px) {
        padding: 7.5px  15px;
    }

    &__wrap {
        display: flex;
        align-items: center;

        
        @include below(1240px) {
            justify-content: space-between;
        }

        @include below(1024px) {
            justify-content: stretch;
        }
    }

    &__logo {
        display: block;
        position: relative;
        color: $white_color;
        z-index: 301;
        width: 60px;
        height: 60px;

        @include below(1024px) {
            margin: 0px auto 0  435px;
            width: 50px;
            height: 50px;
        }

        @include below(990px) {
            margin: 0px auto 0  420px;
        }

        @include below(980px) {
            margin: 0px auto 0  410px;
        }

        @include below(950px) {
            margin: 0px auto 0  395px;
        }

        @include below(920px) {
            margin: 0px auto 0  380px;
        }

        @include below(900px) {
            margin: 0px auto 0  370px;
        }

        @include below(860px) {
            margin: 0px auto 0  355px;
        }

        @include below(840px) {
            margin: 0px auto 0  340px;
        }

        @include below(820px) {
            margin: 0px auto 0  330px;
        }

        @include below(800px) {
            margin: 0px auto 0  320px;
        }

        @include below(770px) {
            margin: 0px auto 0  310px;
        }

        @include below(750px) {
            width: 40px;
            height: 40px;
            margin: 0px auto 0 300px
        }

        @include below(720px) {
            margin: 0px auto 0 290px
        }

        @include below(700px) {
            margin: 0px auto 0 280px
        }

        @include below(690px) {
            margin: 0px auto 0 273px
        }

        @include below(680px) {
            margin: 0px auto 0 265px
        }

        @include below(670px) {
            margin: 0px auto 0 260px
        }

        @include below(660px) {
            margin: 0px auto 0 255px
        }

        @include below(650px) {
            margin: 0px auto 0 250px
        }

        @include below(630px) {
            margin: 0px auto 0 240px
        }

        @include below(620px) {
            margin: 0px auto 0 235px
        }


        @include below(590px) {
            margin: 0px auto 0 230px
        }

        @include below(580px) {
            margin: 0px auto 0 225px
        }

        @include below(570px) {
            margin: 0px auto 0 220px
        }

        @include below(560px) {
            margin: 0px auto 0 215px
        }

        @include below(550px) {
            margin: 0px auto 0 210px
        }

        @include below(540px) {
            margin: 0px auto 0 205px
        }

        @include below(530px) {
            margin: 0px auto 0 200px
        }

        @include below(520px) {
            margin: 0px auto 0 195px
        }

        @include below(510px) {
            margin: 0px auto 0 190px
        }

        @include below(500px) {
            margin: 0px auto 0 185px
        }

        @include below(490px) {
            margin: 0px auto 0 180px
        }

        @include below(480px) {
            margin: 0px auto 0 175px
        }

        @include below(470px) {
            margin: 0px auto 0 170px
        }

        @include below(460px) {
            margin: 0px auto 0 165px;
        }

        @include below(450px) {
            margin: 0px auto 0 160px;
        }

        @include below(440px) {
            margin: 0px auto 0 155px;
        }

        @include below(430px) {
            margin: 0px auto 0 150px;
        }

        @include below(420px) {
            margin: 0px auto 0 140px;
        }

        @include below(400px) {
            margin: 0px auto 0 135px;
        }

        @include below(390px) {
            margin: 0px auto 0 130px;
        }

        @include below(380px) {
            margin: 0px auto 0 123px;
        }

        @include below(365px) {
            margin: 0px auto 0 117px;
        }

        svg {
           width: 100%;
           height: 100%;
        }
    }

    &__nav {
        flex: auto;
          
        @include below(1240px) {
            flex: none;
        }

        @include below(1024px) {
            display: none;
        }
    }

    &__list {
        display: flex;
        align-items: center;

        &::before {
            content: "";
            display: inline-block;
            flex-shrink: 0;
            background-color: $white_color;
            height: 1px;
            flex: auto;
            margin: 0 32px 0 100px;

            @include below(1800px) {
                margin: 0 32px;    
            }
        }

        &::after {
            content: "";
            display: inline-block;
            background-color: $white_color;
            height: 1px;
            flex: auto;
            margin: 0 100px 0 32px;

            @include below(1800px) {
               margin: 0 32px;    
            }
        }

        @include below(1240px) {
            &::before,
            &::after {
                display: none;
            }    
        }

        

        &-item {
            &:not(:last-child) {
                margin-right: 40px;
            }
        }

        &-link {
            white-space: nowrap;
            color: $white_color;
            font-size: 13px;
            font-weight: 500;
            line-height: 100%;
            text-transform: uppercase;
            transition: color .3s linear;

            &:hover,
            &:active {
                color: #A1A1A1;
            }
        }
    }

    &__right {
        position: relative;
        display: flex;
        z-index: 301;
    }

    &__link {
        color: $white_color;
        transition: color .3s linear;
        width: 24px;
        height: 24px;

        @include below(768px) {
            width: 20px;
            height: 20px;
        }

        &:hover,
        &:active {
            color: #A1A1A1;
        }

        &:not(:last-child) {
            margin-right: 18px;

            @include below(768px) {
               margin-right: 12px;
            }
        }
        svg {
            width: 100%;
            height: 100%;
        }

        &-numb {
            position: absolute;
            right: -4px;
            bottom: -3px;
            width: 16px;
            height: 16px;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50px;
            background-color: $orange_color;
            color: $white_color;
            font-size: 9px;
            font-style: normal;
            font-weight: 500;
            line-height: 100%;

            @include below(768px) {
                right: -5px;
                bottom: -5px;
            }
        }
    }

    &__burger {
        display: none;
        padding: 3px;

        @include below(1024px) {
            position: relative;
            display: block;
            z-index: 301;
        }

        
        &.is-change {
            .header__burger-line1 {
                transform: rotate(-45deg) translate(-5px, 4px);
            }

            .header__burger-line2 {
                opacity: 0;
            }

            .header__burger-line3 {
                width: 25px;
                transform:  rotate(45deg) translate(-6px, -5px);
            }
        }
    }

    &__burger-line1,
    &__burger-line2,
    &__burger-line3 {
        display: block;
        width: 25px;
        height: 2px;
        background-color: $white_color;
        margin: 5px 0;
        transition: transform .3s linear, opacity .3s linear;
    }

    &__burger-line3 {
        width: 12.5px;
    }

    &__menu {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        max-width: 500px;
        height: 100vh;
        padding: 120px 20px 20px 140px;
        background-color: #454545;
        transform: translateX(-100%);
        visibility: hidden;
        overflow-y: auto;
        z-index: 250;
        display: none;
        transition: transform .3s linear, visibility .3s linear, display .3s linear;

        @include below(1024px) {
            display: block;
        }

        @include below(768px) {
            padding: 100px 20px 20px 60px;
        }

        @include below(640px) {
           max-width: initial;
        }

        @include below(540px) {
            padding: 70px 20px 20px 40px;
        }

        &.is-show {
            visibility: visible;
            transform: translateX(0);
        }

        .header__nav {
            display: block;
        }

        .header__list {
            flex-direction: column;
            align-items: start;

            &-item {
                &:not(:last-child) {
                    margin: 0 0 20px;
                }

            }
        }

    }
}