@import "main-bx";

.photogallery {
     
    margin-top: 20px;
    
    &__slider  {
        display: flex;
        align-items: stretch;
        margin: 0 -7.5px;

        @include below(670px) {
            margin: 0 -5px;

            &.slick-initialized .slick-slide {
                opacity: 0.4;
             }

            &.slick-initialized .slick-slide.slick-current{
               opacity: 1;
            }
        }


    }

    &__item {
        width: calc(33.333% - 15px);
        margin: 0 7.5px;

        &:not(:nth-child(-n+3)):not(.slick-slide) {
            display: none;
        }


        @include below(1240px) {
            width: calc(50% - 15px);

            &:not(:nth-child(-n+2)):not(.slick-slide) {
                display: none;
            }
        }

        @include below(670px) {
            width: 100%;
            max-width: 500px;
            margin: 0 5px;

            &:not(:first-child):not(.slick-slide) {
                display: none;
            }
        }

        @include below(570px) {
            max-width: 390px;
        }

        @include below(450px) {
            max-width: 280px;
        }
    }

    &__btn {
        position: absolute;
        top: 50%;
        // transform: translate(0, -50%);
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 40px;
        background-color: $white_color;
        color: $black_color;
        z-index: 10;

        @include below(1240px) {
            width: 40px;
            height: 40px;
        }

        @include below(670px) {
           display: none;
        }

        &:hover {
            background-color: $black_color;
            color: $white_color;
        }

        svg {
            width: 24px;
            height: 24px;

            @include below(1240px) {
                width: 20px;
                height: 20px;
            }

        }

        &_prev {
            left: -10px;
        }

        &_next {
            right: -10px;
        }
    }
}