@import "main-bx";

.pagination {

    &__list {
        display: flex;
        align-items: center;
        margin: 0 -2px;

        @include below(590px) {
            justify-content: center;
        }
    }

    &__item {
        margin: 0 2px;
    }

    &__link {
        width: 40px;
        height: 40px;
        border-radius: 50px;
        background-color: #F4F4F4;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        font-weight: 500;
        line-height: 24px; 
        color: $black_color;
        transition: background-color .3s linear, color .3s linear;

        &:hover {
            background-color: #5A5C69;
            color: $white_color;
        }

        &:active {
            background-color: $black_color;
            color: $white_color;
        }

        &_active {
            background-color: $black_color;
            color: $white_color;
        }
    }
}