@import "main-bx";

.video {
    margin: 20px 0 0;
    
    &__btn {
        position: absolute;
        top: 50%;
        // transform: translate(0, -50%);
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 40px;
        background-color: $white_color;
        color: $black_color;
        z-index: 10;

        &:hover {
            background-color: $black_color;
            color: $white_color;
        }

        @include below(1240px) {
            width: 40px;
            height: 40px;
        }

        @include below(670px) {
           display: none;
        }

        svg {
            width: 24px;
            height: 24px;

            @include below(1240px) {
                width: 20px;
                height: 20px;
            }
        }

        &_prev {
            left: -10px;
        }

        &_next {
            right: -10px;
        }
    }

    &__wrapper {
        display: flex;

        .active {
            .video__play {
                opacity: 0;
            }
        }

        .slick-track {
            margin-left: -33%;

            @include below(1024px) {
                margin-left: -67%;
            }
        }

        &.slick-initialized .slick-slide {
            opacity: 0.4;
        }

        &.slick-initialized .slick-slide.slick-active {
           opacity: 1;
        }
    }
    
    &__item {
        overflow: hidden;
        position: relative;
        margin-right: 15px;
        min-height: 460px;

        &.slick-slide {
            min-height: 460px;

            @include below(1380px) {
                min-height: 390px;
            }

            @include below(1240px) {
                min-height: 310px;
            }

            @include below(1024px) {
                min-height: 280px;
            }

            @include below(670px) {
                min-height: 260px;
                margin-right: 10px;
            }

            @include below(640px) {
                min-height: 240px;
            }

            @include below(600px) {
                min-height: 200px;
            }

            @include below(540px) {
                min-height: 180px;
            }

            @include below(479px) {
                min-height: 145px;
            }
        }

    
        &:not(:first-child):not(.slick-slide) {
            display: none;
        }
        video {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 10;
            width: 100%;
            height: 100%;
        }
    }
    
    &__play {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 20;
        width: 100px;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.80);
        filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.04));
        backdrop-filter: blur(5px);
        color: $orange_color;

        @include below(1240px) {
            width: 80px;
            height: 80px;
        }

          
        @include below(1024px) {
            width: 50px;
            height: 50px;
        }
    
        svg {
            width: 40px;
            height: 40px;

            @include below(1240px) {
                width: 30px;
                height: 30px;
            }

            
            @include below(1024px) {
                width: 20px;
                height: 20px;
            }
        }

        &:hover {
            opacity: .8;
        }
    }
}

