@import "main-bx";

.buy {
    padding: 130px 0 100px;

    &.buy_second {
        padding: 0 0 100px;

        @include below(768px) {
            padding: 0 0 80px;
        }

        @include below(540px) {
            padding: 30px 0 80px;
        }
    }

    @include below(1240px) {    
        padding: 80px 0 100px;
    }

    @include below(1240px) {    
        padding: 20px 0 80px;
    }

    @include below(768px) {    
        padding: 0 0 60px;
    }

    &__title {
        text-align: center;
        font-size: 70px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 40px;

        &_start {
            text-align: start;
        }

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
            text-align: start;
            margin-bottom: 24px;
        }

        @include below(540px) {
            font-size: 26px;
        }
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        background-color: #F4F4F4;
        padding: 16px 0 0 16px;

        @include below(1024px) {
            flex-direction: column-reverse;
            padding: 0;
        }
    }

    &__col {
        width: 100%;
        max-width: 435px;
        @include below(1024px) {
            max-width: initial;
            padding: 15px;
        }
    }

    &__form {
        width: 100%;
        max-width: 403px;
        margin-bottom: 6px;
        @include below(1024px) {
            max-width: initial;
            margin-bottom: 12px;
        }
    }

    &__form-label {
        width: 100%;
        position: relative;
        display: block;
    }

    &__search {
        width: 100%;
        padding: 16px 46px;
        background-color: $white_color;
        font-size: 13px;
        font-weight: 400;
        line-height: 100%; 
        color: #5A5C69;
    }

    &__search-icon {
        position: absolute;
        top: 13px;
        left: 11px;

        svg {
            width: 24px;
            height: 24px;
        }
    }

    &__inner {
        margin-right: 15px;
        height: 532px;
        overflow: hidden;
        overflow-y: auto; 
        &::-webkit-scrollbar {
            width: 3px;
            border-radius: 3px;
            background-color: #BDBEC3;
        }
        &::-webkit-scrollbar-thumb {
            background: #5A5C69;
            border-radius: 3px;
        }

        
        @include below(1024px) {
           height: 460px;
           margin-right: 0;
        }
    }

    &__item {
        cursor: pointer;
        width: 100%;
        max-width: 403px;
        background-color: $white_color;
        padding: 25px 16px 23px;
        margin-bottom: 6px;

        @include below(1024px) {
            max-width: initial;
        }

        @include below(575px) {
            padding: 12px;
        }

        &.is_active {
            .buy__item-icon {
                color: $orange_color;
            }
        }

        &-title {
            font-size: 20px;
            font-weight: 700;
            line-height: 100%;
            text-transform: uppercase;
            margin-bottom: 12px;

            @include below(575px) {
                font-size: 16px;
            }
        }

        &-row {
            display: flex;
        }

        &-right {
            flex: auto;
            display: flex;
            flex-direction: column;
        }

        &-icon {
            color: #5A5C69;
            margin-right: 10px;
            svg {
                width: 20px;
                height: 40px; 

                @include below(575px) {
                    width: 16px;
                    height: 32px;
                }
            }
        }

        &-address {
            display: flex;
            flex-direction: column;

            strong {
                font-size: 16px;
                font-weight: 700;
                line-height: 100%; 
                text-transform: uppercase;
                margin-bottom: 10px;
                color: #5A5C69;

                @include below(575px) {
                    font-size: 14px;
                    margin-bottom: 4px;
                }
            }

            span {
                font-size: 13px;
                font-weight: 500;
                line-height: 100%; 
                text-transform: uppercase;

                @include below(575px) {
                    font-size: 12px;
                }
            }
        }

        &-bottom {
            margin-top: 14px;
            display: flex;
            justify-content: space-between;
            align-items: end;
            font-size: 12px;
            line-height: 100%;

            @include below(575px) {
                flex-direction: column;
                align-items: start;
            }
            a {
                display: inline-block;
                color: #5A5C69;
            }
        }

        &-mail {
            margin-top: 6px;
        }
    }

    &__mail {
        display: inline-block;
        margin-left: auto;
        margin-top: 4px;
        font-size: 12px;
        font-weight: 400;
        line-height: 100%;
        color: #5A5C69;

        @include below(575px) {
            margin-left: 0;
            margin-top: 6px;
        }
    }

    &__map {
        width: 100%;
        max-width: 950px;
        height: 604px;

        .ymaps-2-1-79-copyrights-pane {
            display: none;
        }

        .ymaps-2-1-79-ground-pane {
            filter: grayscale(100%);
            -webkit-filter: grayscale(100%);
            -moz-filter: grayscale(100%);
        }

        @include below(1024px) {
            max-width: initial;
            height: 400px;
        }

        @include below(575px) {
            height: 240px;
        }
    }
}