@import "main-bx";

.shopping {
    padding-bottom: 100px;

    @include below(768px) {
        padding: 0 0 80px;
    }

    @include below(540px) {
        padding: 30px 0 80px;
    }

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;

        @include below(768px) {
            align-items: start;
            margin-bottom: 20px;
        }
    }

    &__wrap {
        display: flex;
        align-items: start;

        @include below(1240px) {
            flex-direction: column;
        }
    }

    &__title {
        font-size: 70px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
        }

        @include below(540px) {
            font-size: 26px;
        }
    }

    &__text {
        font-size: 32px;
        font-weight: 400;
        line-height: 110%; 
        color: #A1A1A1;

        @include below(1240px) {
            font-size: 26px;
        }

        @include below(1024px) {
            font-size: 20px;
        }

        @include below(479px) {
            font-size: 14px;
        }
    }

    &__cards {
        width: 100%;
        max-width: 1050px;
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin-right: -4px;
        margin-top: 1px;
        @include below(1380px) {
            max-width: 850px;
        }

        @include below(1240px) {
            max-width: initial;
        }

    }

    &__card {
        width: 33.333%;
        margin-top: -1px;
        margin-right: -1px;

        @include below(1380px) {
            width: 50%;
        }
    }

    &__info {
        width: 100%;
        max-width: 350px;
        background-color: #F4F4F4;
        padding: 40px;

        @include below(1240px) {
            padding: 30px;
            margin-top: 20px;
            max-width: initial;
        }

        @include below(540px) {
            padding: 20px 15px;
        }
    }

    &__info-title {
        font-size: 24px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        margin-bottom: 32px;

        @include below(1240px) {
            br {
                display: none;
            }
        }

        @include below(768px) {
            font-size: 20px;
            margin-bottom: 24px;
        }

        @include below(540px) {
            font-size: 18px;
        }
    }

    &__info-btn {
        width: 100%;
        margin-top: 28px;

        svg {
            margin-left: 10px;
            width: 20px;
            height: 20px;
        }

        @include below(1240px) {
            width: fit-content;
        }

        @include below(768px) {
           margin-top: 12px;
        }

        @include below(540px) {
            width: 100%;
        }
    }

    &__info-item {
        display: flex;
        justify-content: space-between;
        font-size: 16px;
        line-height: 120%; 
        color: #5A5C69;
        margin-bottom: 32px;

        &::after {
            content: "";
            display: block;
            flex: auto;
            order: 2;
            border-bottom: 1px solid $grey_color;
            margin: 0 10px;
        }

        span {
            order: 1;
            font-weight: 400;
        }

        strong {
            order: 3;
            font-weight: 600;
        }

    }

    &__info-price {
        display: flex;
        flex-direction: column;
        color: #5A5C69;

        span {
            display: inline-block;
            font-size: 16px;
            font-weight: 400;
            line-height: 100%;
            margin-bottom: 12px;
        }
        strong span,
        strong {
            font-size: 32px;
            font-weight: 600;
            line-height: 100%;

            @include below(768px) {
                font-size: 28px;
            }

            @include below(540px) {
                font-size: 24px;
            }
        }
    }

    &__form {
        background-color: #F4F4F4;
        padding: 40px;
        max-width: 1050px;
        margin-top: 40px;

        @include below(1240px) {
            max-width: initial;
        }
        
        @include below(540px) {
            margin-top: 20px;
            padding: 20px 15px;
        }
    }
}