@import "main-bx";

.benefits {

    &__title {
        text-align: center;
        font-size: 32px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 30px;

        @include below(1240px) {
            font-size: 28px;
        }

        @include below(1024px) {
            font-size: 24px;
        }

        @include below(768px) {
            font-size: 20px;
            text-align: start;
            margin-bottom: 20px;
        }

        @include below(540px) {
            font-size: 18px;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: -20px -10px 0;

        @include below(768px) {
            margin: -15px 0 0;
        }

        @include below(540px) {
            margin: -6px 0 0;
        }
    }

    &__item {
        width: calc(50% - 20px);
        margin: 20px 10px 0;
        padding: 30px;
        border: 1px solid #D9D9D9;
        display: flex;

        @include below(768px) {
            width: 100%;
            margin: 15px 0 0;
            padding: 20px 30px 20px 20px;
        }

        @include below(540px) {
            margin: 6px 0 0;
        }
    }

    &__item-numb {
        display: block;
        font-size: 24px;
        font-weight: 500;
        line-height: 100%; 
        margin-right: 20px;

        @include below(540px) {
            font-size: 20px;
            margin-right: 10px;
        }
    }

    &__item-text {
        font-size: 15px;
        font-weight: 300;
        line-height: 140%;
        color: #5A5C69;
    }
}