@import "main-bx";

.error-block {
    padding: 0 0 100px;

    @include below(768px) {
        padding: 0 0 80px;
    }

    @include below(540px) {
        padding: 30px 0 80px;
    }

    &__title {
        font-size: 70px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;

        @include below(1540px) {
            font-size: 60px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(768px) {
            font-size: 35px;
        }

        @include below(540px) {
            font-size: 26px;
        }
    }

    &__wrap {
        position: relative;
    }

    &__img {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        max-width: 1208px;
        z-index: 0;
    }

    &__content {
        padding: 116px 0 160px;
        position: relative;
        z-index: 5;

        @include below(768px) {
            padding: 116px 0 120px;
        }

        @include below(575px) {
            padding: 160px 0 0;
        }
    }

    &__subtitle {
        font-size: 32px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 24px;

        @include below(1240px) {
            font-size: 28px;
        }

        @include below(768px) {
            font-size: 24px;
        }

        @include below(575px) {
            font-size: 18px;
        }
    }

    &__text {
        font-size: 15px;
        font-style: normal;
        font-weight: 300;
        line-height: 140%;
        margin-bottom: 40px;
        color: #5A5C69;

        @include below(768px) {
            margin-bottom: 24px;
        }

        @include below(575px) {
            br {
                display: none;
            }
        }
    }

    &__btn {
        svg {
            width: 20px;
            height: 20px;
            margin-left: 10px;
        }
    }
}