@import "main-bx";

.select {
    width: 100%;
    height: 46px;
    padding: 15px 12px;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 105%;
    color: #5A5C69;
    border-radius: 10px;
    background-color: $white_color;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';

    & ~ .select2-container {
        width: 100%; 

        .select2-selection--single {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            height: 46px;
            padding: 15px 12px;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 105%;
            color: #5A5C69;
            border-radius: 10px;
            background-color: $white_color;
            border: 1px solid transparent;

            &:hover {
                border-color: #DEDEDE;
            }

            &:focus {
                border-color: $black_color;
            }

                
            .select2-selection__rendered {
                padding: 0;
                display: flex;
                align-items: center;
            }

            .select2-selection__placeholder {
                color: #5A5C69;
            }
        }

    

        .select2-selection__arrow {
            height: 30px;
            width: 30px;
            background-image: url("../img/sprites/sprite.svg#select_icon");
            background-position: center;
            background-repeat: no-repeat;
            transition: transform .3s linear;
            top: 10px;
            right: 5px;

            b {
                display: none;
            }
        }

     
    }

    &.error ~ .select2-container {
            &.select2-container--default .select2-selection {
                color: $orange_color;
                border-color: $orange_color;
            }

            .select2-selection__placeholder {
                color: $orange_color;
            }

            .select2-selection__arrow {
                height: 30px;
                width: 30px;
                background-image: url("../img/sprites/sprite.svg#down_icon");
                background-position: center;
                background-repeat: no-repeat;
                transition: transform .3s linear;
                top: 10px;
                right: 5px;
    
                b {
                    display: none;
                }
            }
        
    }

    & ~ .select2-container--default .select2-selection--single .select2-selection__arrow {
        height: 30px;
        width: 30px;
        top: 10px;
        right: 5px;
    }

    & ~ .select2-container {
        &.select2-container--open {
            
            .select2-selection__arrow {
                background-image: url("../img/sprites/sprite.svg#down_icon");
                transform: rotate(180deg);
            }
        }
    }
}

.select2-container--default .select2-results {
    .select2-results__options {
        &::-webkit-scrollbar {
            width: 3px;
            background-color: #F4F4F4;
            border-radius: 3px;
        }
 
        &::-webkit-scrollbar-thumb {
            background-color: $orange_color;
            border-radius: 3px;
        }
    }
 }

.select2-container.select2-container--default {
    .select2-dropdown {
        border: none;
        border-radius: 10px;
        padding: 10px 10px;
        box-shadow: 0px 10px 20px -10px #00000026;

        .select2-search {
            display: none;
        }
    }

    .select2-results__option {
        padding: 10px 10px;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        color: #5A5C69;
    }

    // .select2-results__option[aria-selected=true] {
    //     background-color: transparent;
    // }

    .select2-results__option--highlighted[aria-selected] {
        background-color: $orange_color;
    }
}
.select2-container--default.select2-container--open.select2-container--below  {
    .select2-selection--single {
        border-radius: 10px !important;
    }
   
}
